import React from 'react';
import './Result.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCloud, faSun, faCloudRain, faMoon, faCloudMoon, faCloudMoonRain, faSnowflake} from '@fortawesome/free-solid-svg-icons';
import rain from "./images/rain.jpg";
import sun from "./images/sun.jpg";
import night from "./images/night.jpg";
import rainnight from "./images/rainnight.jpg";
import clouds from "./images/clouds.jpg";
import nightclouds from "./images/nightClouds.jpg";
import snow from "./images/snow.jpg";
import nightSnow from "./images/nightSnow.jpg"

const Result = props => {

  const { date, city, coss, weatherState, sunrise, sunset, temp, pressure, wind, err } = props.weather;
  const sunriseTime = new Date(sunrise * 1000).getTime();
  const sunsetTime = new Date(sunset * 1000).getTime();
  const partOfDay = sunriseTime < coss && coss < sunsetTime ? true : false;

  let content = null;

  if (!err && city) {
    

    content = (
      <>
        <div class="allData">
          {((weatherState === "Clouds" || weatherState === "Haze" || weatherState === "Mist")&& partOfDay === true && <FontAwesomeIcon className="icon" icon={faCloud} />) ||
          ((weatherState === "Clouds" || weatherState === "Haze" || weatherState === "Mist") && partOfDay === false && <FontAwesomeIcon className="icon" icon={faCloudMoon} />) || 
          (weatherState === "Clear" && partOfDay === true && <FontAwesomeIcon className="icon" icon={faSun} />) ||
          (weatherState === "Clear" && partOfDay === false && <FontAwesomeIcon className="icon" icon={faMoon} />) ||
          (weatherState === "Snow" && partOfDay === true && <FontAwesomeIcon className="icon" icon={faSnowflake} />) ||
          (weatherState === "Snow" && partOfDay === false && <FontAwesomeIcon className="icon" icon={faCloudMoonRain} />) ||
          (weatherState === "Rain" && partOfDay === false && <FontAwesomeIcon className="icon" icon={faCloudMoonRain} />) ||
          (weatherState === "Rain" && partOfDay === true && <FontAwesomeIcon className="icon" icon={faCloudRain} />)
          }
          <div class="data">
            <h3>Wyniki wyszukiwania dla <em>{city}</em></h3>
            <h4>Dane dla dnia i godziny: {date}</h4>
            <h4>Aktualna temperatura: {temp} &#176;C</h4>
            <h4>Pogoda: {weatherState}</h4>
            <h4>Aktualna siła wiatru {wind} m/s</h4>
            <h4>Aktualna ciśnienie {pressure} hPa</h4>
          </div>
        </div>
      </>
    )

  }

  return (
    <>
      <div className="result">
        {err ? `Nie mamy w bazie ${city}` : content}
      </div>
      <div className="images">
        <img src={sun} className={(weatherState === "Clear" && partOfDay === true)?"active" : "noActive"} alt="sun-day"/>
        <img src={night} className={(weatherState === "Clear" && partOfDay === false)?"active" : "noActive"} alt="night-good-weather"/>
        <img src={rain} className={(weatherState === "Rain" && partOfDay === true)?"active" : "noActive"} alt="rain-day"/>
        <img src={rainnight} className={(weatherState === "Rain" && partOfDay === false)?"active" : "noActive"} alt="rain-day"/>
        <img src={clouds} className={((weatherState === "Clouds" || weatherState === "Haze" || weatherState === "Mist") && partOfDay === true) ?"active" : "noActive"} alt="rain-day"/>
        <img src={nightclouds} className={((weatherState === "Clouds" || weatherState === "Haze" || weatherState === "Mist") && partOfDay === false) ?"active" : "noActive"} alt="rain-day"/>
        <img src={snow} className={(weatherState === "Snow" && partOfDay === true) ?"active" : "noActive"} alt="rain-day"/>
        <img src={nightSnow} className={(weatherState === "Snow" && partOfDay === false) ?"active" : "noActive"} alt="rain-day"/>
      </div>
    </>
  );
}

export default Result;